import React, { useState } from "react";

export default function Textform(props) {
    const handleupclick = () => {
        if (text !== '') {
            let newtext = text.toUpperCase()
            settext(newtext)
            props.showAlert('Converted to UpperCase', 'success')
        }
        else {
            props.showAlert(`Plz enter some characters`, 'warning')
        }
    }
    const handleloclick = () => {
        if (text !== '') {
            let newtext = text.toLowerCase()
            settext(newtext)
            props.showAlert('Converted to LowerCase', 'success')
        }
        else {
            props.showAlert(`Plz enter some characters`, 'warning')
        }
    }
    const handleonchange = (event) => {
        settext(event.target.value)
    }
    const [text, settext] = useState('');

    return (
        <>
            <div className='container' style={{ color: props.mode === "light" ? 'black' : 'white' }}>
                <h1>{props.heading}</h1>
                <div className="mb-3">
                    <textarea
                        value={text}
                        onChange={handleonchange}
                        style={{ backgroundColor: props.mode === "light" ? "white" : "#445f97", color: props.mode === "light" ? "black" : "white" }}
                        className="form-control"
                        placeholder='Enter text here'
                        id="exampleFormControlTextarea1"
                        rows="7"
                    ></textarea>
                    <button disabled={text.length===0} className="btn btn-primary mx-2 my-1" onClick={handleupclick}>Convert to UpperCase</button>
                    <button disabled={text.length===0} className="btn btn-primary mx-2 my-1" onClick={handleloclick}>Convert to LowerCase</button>
                </div>
            </div>
            <div className="container my-3" style={{ color: props.mode === "light" ? 'black' : 'white' }}>
                <h2>{props.heading2}</h2>
                <p>Total words {text.split(/\s+/).filter((element)=>{return element.length!==0}).length} | Total characters {text.length}</p>
                <p>{0.008 * text.split(" ").filter((element)=>{return element.length!==0}).length} Minutes read</p>
                <h2>Preview</h2>
                <p>{text}</p>
            </div>
        </>
    );
}
